@import '../abstracts/variables';
@import '../abstracts/mixins';

body {
  position: relative;
  background: $general-white-color;
  font-family: $font-family;
  font-style: normal;
  font-weight: 400;
  color: $text-color;
}

a {
  display: inline-block;
  line-height: 1.16;
  text-decoration: none;
  font-size: 24px;
  color: $primary-color;

  // For mobile tablets:
  @media screen and (max-width: $screen-sm) {
    font-size: 20px;
  }

  // For mobile phones:
  @media screen and (max-width: $screen-md) {
    font-size: 12px;
  }
}

h2 {
  font-weight: 500;
  @include font-size(40px);
  line-height: $line-height;
  text-align: center;

  //For mobile tablets:
  @media screen and (max-width: $screen-sm) {
    @include font-size(34px);
  }

  // For mobile phones:
  @media screen and (max-width: $screen-md) {
    @include font-size(28px);
  }
}

h3 {
  font-weight: 400;
  @include font-size(24px);
  line-height: $line-height;

  //For mobile tablets:
  @media screen and (max-width: $screen-sm) {
    @include font-size(20px);
  }

  // For mobile phones:
  @media screen and (max-width: $screen-md) {
    @include font-size(15px);
  }
}

h4 {
  font-weight: 400;
  @include font-size(18px);
  line-height: $line-height;
  color: $primary-color;

  //For mobile tablets:
  @media screen and (max-width: $screen-sm) {
    @include font-size(16px);
  }

  // For mobile phones:
  @media screen and (max-width: $screen-md) {
    @include font-size(15px);
  }
}
